import { Component, OnInit } from '@angular/core';
import { SharedComponents } from 'src/app/shared.components';
import { FOPModel } from 'src/models/fop/fop.model';
import { AlertService } from 'src/services/alert.service';
import { ITransaction } from 'src/interfaces/i.transaction.interface';
import { ActivatedRoute } from '@angular/router';
import { ContainerService } from 'src/services/container.service';
import { IContainer } from 'src/interfaces/i.container.interface';

@Component({
  selector: 'app-freight',
  templateUrl: './containers.component.html',
  styles: [
  ]
})
export class ContainersComponent extends SharedComponents implements OnInit {
  skip: number = 0;
  limit: number = 50;
  fopSearch: string;
  fop: FOPModel = new FOPModel();

  constructor(public service: ContainerService) { super() }

  ngOnInit(): void {
    this.service.initContinersFilterForm();
    this.service.seo.updateSEO("containersReport");
    this.getEntiries(true);
    this.service.onUpdateContainerNumber.subscribe(newParem=>this.getEntiries(true))
  }


  /**
 * Get My Users
 */
  getEntiries(isReset): void {
    if (isReset) {
      this.service.containersSelected=[];
      this.service.freightsSelected=[];

      this.skip = 0;
      this.service.freightsContainerGroup = [];
      this.fop = new FOPModel(this.fop.fieldNames, this.service.freightsContainerGroup.length);
    }
    this.fop.paging.isRequestingToServer = true;

    //Get Users Now From Server
    this.allSubscriptions.push(
      this.service.http.getContainersReport<IContainer[]>(this.skip, this.limit, this.service.form.value).subscribe(res => {
        //Update FOP Properties
        if (res.noMoreOfResult)
          this.fop.paging.disableGetMoreButton = true;
        this.fop.paging.isRequestingToServer = false;

        if (res.isSuccess) {
          this.service.freightsContainerGroup = [...this.service.freightsContainerGroup, ...res.result];

          //Fop 
          this.fop = new FOPModel(this.fop.fieldNames, this.service.freightsContainerGroup.length);
          this.skip += this.limit;
        } else {
          AlertService.showResponseMessage(res);
        }
      }, error => {
        AlertService.canRequestError(error);
        this.fop.paging.isRequestingToServer = false;
      }, () => {
        this.fop.paging.isRequestingToServer = false;
      }));
  }


  funTrack(index: number, entitiy: ITransaction): string | undefined {
    return entitiy?._id;
  }
}//End CLass
