import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { ContainersComponent } from "src/app/back-end/containers/containers/containers.component";
import { EditContainerDetailsComponent } from "src/app/back-end/containers/edit-container-details/edit.container.details.component";
import { FreightsChangeContainerNumberComponent } from "src/app/back-end/containers/freights-change-container-number/freights.change.container.number.component";
import { FreightsUndemandFromContainerNumberComponent } from "src/app/back-end/containers/freights-undemand-from-containers/freights.undemand.from.containers.component";
import { AdminAuthCanActive } from "src/services/authentcation/admin.auth.can.active.service";

 
const ROUTS: Routes = [
    {
        path: '', component: ContainersComponent, canActivate: [AdminAuthCanActive],
        children: [
            {path: "editContainerDetails",component: EditContainerDetailsComponent},
            {path: "chageContainerNumber",component: FreightsChangeContainerNumberComponent},
            {path: "undemandFromContainer",component: FreightsUndemandFromContainerNumberComponent}
        ]
    },
];

@NgModule({
    imports: [RouterModule.forChild(ROUTS)],
    exports: [RouterModule]
})
export class ContainersRoutingModule {

}

