import { ResponseTypeEnum } from 'src/enums/response.type.enum';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SharedComponents } from 'src/app/shared.components';
import { AlertService } from 'src/services/alert.service';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { HttpService } from 'src/services/http/http.service';
import { ITransaction } from 'src/interfaces/i.transaction.interface';
import { FreightCasesEnum } from 'src/enums/freight.cases.enum';
import { freightCasesArray } from 'src/consts/freight.cases.const';
import { ContainerService } from 'src/services/container.service';

@Component({
  selector: 'app-add-freights-to-container',
  templateUrl: './edit.container.details.component.html',
  styles: [
  ]
})
export class EditContainerDetailsComponent extends SharedComponents implements OnInit {
  constructor(public u: CurrentUserService, public service: ContainerService, public http: HttpService,
    private activateRouter: ActivatedRoute) { super() }

  ngOnInit(): void {

    if (!this.service.containersSelected.length) {
      AlertService.showResponseMessage({
        responseType: ResponseTypeEnum.error,
        message: this.u.r.youMustSelectContainersFirst
      } as any);
      this.service.closeModel();
    }

    this.service.initEditDetailsForm();

  }

  /**
   * Delete Item
   */
  onSubmit() {
    if (this.service.form.invalid) {
      this.service.isFormSubmitError = true;
      return;
    }
    this.service.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.service.http.updateFreightsByContainersNumbers<ITransaction>({
        containerNumbers: this.service.containersSelected.map(c => c.containerNumber),
        updateingData: this.service.form.value
      })
        .subscribe(res => {
          AlertService.showResponseMessage(res);
          this.service.isRequestingToServer = false;
          if (res.isSuccess) {
            this.service.onUpdateContainerNumber.emit();
            //Back To Component
            this.service.closeModel();
          }
        }, error => {
          this.service.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }


}//End Class
