import { IPort } from './../interfaces/i.port.interface';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { EventEmitter, Injectable } from "@angular/core";
import { HttpService } from './http/http.service';
import { CurrentUserService } from './authentcation/current.user.service';
import { SEOService } from './seo.service';
import { AlertService } from './alert.service';
import { carSizesArray } from 'src/consts/car.sizes.const';
import { IGeneralSetting } from 'src/interfaces/i.general.setting.interface';
import { IState } from 'src/interfaces/i.state.interface';
import { ITransaction } from 'src/interfaces/i.transaction.interface';
import { Router } from '@angular/router';
import { freightCasesContainerArray, freightCasesArray } from 'src/consts/freight.cases.const';
import { IContainer } from 'src/interfaces/i.container.interface';
import { FreightCasesEnum } from 'src/enums/freight.cases.enum';
import { PortTypesEnum } from 'src/enums/port.types.enum';
import { CustomValidatorsService } from './custom.validators.service';

@Injectable()
export class ContainerService {

    freightCases = freightCasesArray;
    buySources: IGeneralSetting[];
    arrivalPorts: IPort[] = [];
    navigationalWayes: IGeneralSetting[] = [];
    form: FormGroup = new FormGroup({});
    isFormSubmitError: boolean;
    isRequestingToServer: boolean;
    freightsSelected: ITransaction[] = [];
    freightsContainerGroup: IContainer[] = [];
    containersSelected: IContainer[] = [];


    freightCasesToChange = freightCasesContainerArray;

    onUpdateContainerNumber = new EventEmitter<any>();
    constructor(public u: CurrentUserService, public seo: SEOService, public http: HttpService, public router: Router) {

    }

    /** Init Continers Filter Form */
    initContinersFilterForm() {
        this.form = new FormGroup({
            containerNumber: new FormControl()
        });
    }

    /** Init Edit Details Form */
    initEditDetailsForm() {
        this.form = new FormGroup({
            freightCaseId: new FormControl(FreightCasesEnum.Loaded_in_a_container, Validators.required),
            navigationalWayeId: new FormControl(null),
            trackLink: new FormControl(null,CustomValidatorsService.invalidUrl),
            arrivalDate: new FormControl(null),
            arrivalPortId: new FormControl(null),
            containerPrice: new FormControl(null, CustomValidatorsService.invalidNumber)
        });
        this.getItems(["arrivalPorts", "navigationalWayes"]);
    }

    /**Get Items To Select From Them */
    getItems(itemsNames: string | string[]): void {
        this.http.getSelectItems<any>(itemsNames as []).subscribe(res => {
            if (res.isSuccess) {
                (itemsNames as []).forEach((itemName: string) => this[itemName] = res.result[itemName])
            } else {
                AlertService.showResponseMessage(res);
            }
        }, error => {
            AlertService.canRequestError(error);
        })
    }


    /**Select Freight */
    selectFreight(item: ITransaction) {
        item.IsSelected = !item.IsSelected;
        if (item.IsSelected) {
            this.freightsSelected.push(item);
        } else {
            this.freightsSelected.splice(this.freightsSelected.findIndex(f => f._id == item._id), 1);

        }
    }

    /**Select Container */
    selectContainer(item: IContainer) {
        item.IsSelected = !item.IsSelected;
        if (item.IsSelected) {
            this.containersSelected.push(item);
        } else {
            this.containersSelected.splice(this.containersSelected.findIndex(f => f.containerNumber == item.containerNumber), 1);

        }
    }

    /**
   * Close: Back To The Page
   */
    public closeModel() {
        this.isFormSubmitError = false;
        this.form.reset();
        this.router.navigate(['/containers']);
    }

}//End Class